ul.tick-list {
    list-style: none;
    padding: 0;
    margin: 0;
  }
  
  ul.tick-list li {
    display: flex;
    align-items: center;
  }
  
  ul.tick-list li svg {
    width: 16px;
    height: 16px;
    fill: rgb(var(--primary)); /* Change only the tick color */
    margin-right: 8px;
  }
  

  ul.footer {
    list-style: none;
    padding: 0; /* Removes default padding */
    margin: 0; 
    color: rgb(var(--onSurfaceVariant));
  }

  ul.footer li {
    display: flex;
    align-items: center;
  }