@use '../styles/size' as *;
@use '../styles/shape' as *;
@use '../styles/elevation' as *;
@use '../layouts/breakpoints' as *;
@use '../styles/theme' as *;

/* Navigation Drawer*/


.brand-logo-container{
    padding: size(7);
    display: flex;
    align-items: center;
    justify-content: left;
}

.brand-logo {
    display: flex;
    color: rgb(var(--onSurface));
    max-width: $logo-max-width;
    max-height: $logo-max-height;  
  
    // Invert logo when dark theme is active
    .dark-theme & {
      filter: invert(1);
    }
  }





.navigation-drawer{
    background-color: rgb(var(--surfaceContainerLow));
    color: rgb(var(--onSurface));
    padding: size(3);
    display: none;
    flex-direction: column;
    flex: 1;

}

ul.nav-list {
    list-style-type: none;
    margin: 0;
    padding: 0;
  }

  ul.nav-list-horizontal {
    display: flex;
    list-style-type: none;
    margin: 0;
    padding: 0;

    @include window-size(compact) {
        display: none; // Hide horizontal list on small screens
    }

    @include window-size(medium) {
        display: none; // Hide horizontal list on small screens
    }
  }

li.nav-list-item{
    padding: 0;
}

.nav-link{
    color: inherit;
    height: size(11);
    display: flex;
    align-items: center;
    padding-left: size(7);
    padding-right: size(7);
    border-left: shape(corner-full);
    border-radius: shape(corner-full);
    text-decoration: none;
    &:hover{
        background-color: rgba(var(--onSurface), 0.08);
        font-weight: $weight-bold;

    }
    &:active{
        background-color: rgba(var(--onSurface), 0.12);
    }
    &.current{
        background-color: rgba(var(--onSurface), 0.12);
    }

}

// Top Nagigation Bar

.top-nav-bar{
    height: size(12);
    background: rgb(var(--surfaceContainerLow));
    max-width: 100%;
    display: flex;
    flex-direction: column;
    align-items:    center;
@include elevation(1);
position: fixed;
top: 0;
z-index: 100;
width: 100%;
}

.top-nav-bar-container{
    display: flex;
    justify-content: space-between; /* pushes left & right items to edges */
    align-items: center; 
    height: 100%;
    max-width: 1200px;
    position: relative;
    z-index: 2;
    gap: $padding;
    display: flex;
    width: 100%;
}

.top-nav-bar-brand-logo-container{
    display: flex;
    align-items: center;
    margin-left: size(7);
    height: 100%;
    flex: 0 0 auto;
}

.top-menu-right-icons{
    display: flex;
    justify-content: flex-end;
    margin-right: size(7);
}