@use '../styles/shape' as *;
@use '../styles/size' as *;
@use '../styles/elevation' as *;
@use '../components/buttons' as *;
@use '../layouts/breakpoints' as *;
@use '../styles/motion' as *;

//Regular Card

.card {
    border-radius: shape(corner-medium);
    display: flex;
    flex-direction: column;
    overflow: hidden;
    color: rgb(var(--onSurface));
    position: relative;



    &.outlined {
      border: solid 1px rgb(var(--outlineVariant));
      background: rgb(var(--Surface));
      &:hover{
        @include elevation(1);
        @include layerstate(--onSurface, 0.08);
      }

      &:active{
        @include elevation(0);
        @include layerstate(--onSurface, 0.12);
      }

    }      
  
    &.filled {
      background: rgb(var(--surfaceContainerHighest));
      border: none;
      &:hover{
        @include elevation(1);
        @include layerstate(--onSurface, 0.08);
      }

      &:active{
        @include elevation(1);
        @include layerstate(--onSurface, 0.12);
      }

    }
  
    &.elevated {
      background: rgb(var(--surfaceContainerLow));
      @include elevation(1);
      &:hover{
        @include elevation(2);
        @include layerstate(--onSurface, 0.08);
      }


      &:active{
        @include elevation(1);
        @include layerstate(--onSurface, 0.12);
      }
    }
  }

  .card-image{
    width: 100%;
    object-fit: cover;
    object-position: center center;
    aspect-ratio: 1/1;
  }

  .card-content{
    height: 100%;
    padding: var(--padding);
    display: flex;
    flex-direction: column;
    gap: var(--group);
    z-index: 2;
    container-type: inline-size;
  }




//Review Card
.review-card {
  background-color: rgb(var(--surfaceContainerLowest));
  position: relative;
border-radius: shape(corner-medium);
display: flex;
flex-direction: column;
overflow: hidden;
color: blue;
justify-items: left;
color: rgb(var(--onSurface));
padding-bottom: size(10);
border: solid 1px rgb(var(--outlineVariant));
}

.review-card-quotemark{
  position: absolute;
  top: -10px;
  left: -10px;
  width: 60%;
  height: 60%;
  z-index: 1;
  color: rgba(var(--onSurface), 0.08)
}

.review-location-icon{
  position: absolute;
  bottom: var(--padding);
  right: var(--padding);
  width: size(10);
  height: size(10);  
  z-index: 1;
  color: rgba(var(--onSurface), 0.16)
}


//This is for a card heading with an icon and a subheading
.card-header-icon-subheading{
  display: grid;
  grid-template-columns: auto 1fr; /* two equal-width columns */
  grid-template-rows: auto auto; /* two rows; heights adjust based on content */
  column-gap: var(--group);
  row-gap: size(1);
  align-items: center;;
}

//This is for a card heading with an icon and a title and NO subheading
.card-header-icon-title{
  display: grid;
  grid-template-columns: auto 1fr; /* two equal-width columns */
  grid-template-rows: auto auto; /* two rows; heights adjust based on content */
  column-gap: $group;
  row-gap: size(1);
  align-items: center;
}

//This defines how the content will be displayed in the card header with a subtitle.
.card-header-icon-subheading-content{
  display: flex;
  flex-wrap: wrap;
  column-gap: $group;
  row-gap: size(1);
  &:first-child{
    grid-row: span 2;
  }
}

//This defines how the content will be displayed in the card header with a just a title no subheading.
.card-header-icon-title-content{
  display: flex;
  flex-wrap: wrap;
  column-gap: $group;
  row-gap: size(1);
}

// Caurosel Card


  .caurosel-card {
    border-radius: shape(corner-medium);
    position: relative;  // Needed for absolute positioning of ::after
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    overflow: hidden;
    color: rgb(255, 255, 255);
    height: calc(40vh - 2 * var(--padding));
    aspect-ratio: 1 / 1;
    @include elevation(1);
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    container: inline-size;


    @include window-size(compact) {
      height: 40vw;
    }
  
    &:hover {
      @include elevation(2);
      z-index: 2;
      
      // Update the overlay on hover
      &::after {
        background: 
          linear-gradient(rgba(var(--onSurface), 0.12), rgba(var(--onSurface), 0.12)),
          linear-gradient(to top, rgba(0, 0, 0, 0.8) 0%, transparent 40%);
      }
    }
    
    &:active {
      @include elevation(1);

      &::after {
        background: 
          linear-gradient(rgba(var(--onSurface), 0.12), rgba(var(--onSurface), 0.12)),
          linear-gradient(to top, rgba(0, 0, 0, 0.8) 0%, transparent 40%);
      }
      

    }
    
    &::after {
      content: "";
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      // Base overlay: bottom gradient
      background: linear-gradient(to top, rgba(0, 0, 0, 0.8) 0%, transparent 40%);
      pointer-events: none; /* Allows clicks to pass through */
      z-index: 1;         /* Overlay sits above the inline background image */
    }

  }
  
  
  //This if for making a button sit at the bottom of the card
  .bottom{
    margin-top: auto;
  }

  //Full Height
  .full-height{
    height: 100%;
  }

  .caurosel-card .hidden-hover-content{
    transition: all  #{motion(Standard, Medium-2)} 10ms, opacity #{motion(Standard, Medium-2)} 10ms;
    opacity: 0;
    display: flex;
    flex-direction: column;
    gap: var(--group);
    height: 0;
    overflow: hidden;
  }

  .caurosel-card:hover .hidden-hover-content{
    
    opacity: 1;
    height: auto;
  }

  .top-left{
    position: absolute;
    top: var(--padding);
    left: var(--padding);
  }

  .gap-none{
    gap: 0 !important;
  }


