@use '../styles/size' as *;
@use '../styles/theme' as *;
@use '../styles/typography';
@use '../styles/shape' as *;
@use '../styles/size' as *;

.contact-form{
    display: grid;
grid-template-columns: 1fr 1fr;
    gap: $group;
}

form{margin: 0px}

.contact-form > *:nth-child(n + 3) {
    grid-column: span 2; /* Makes items span across both columns */
  }

  @media (max-width: 400px) {  
    .contact-form {grid-template-columns: 1fr;}

    .contact-form > *:nth-child(n + 3) {
        grid-column: span 1; /* Makes items span across both columns */
      }

}

md-filled-text-field {
    --md-filled-text-field-input-text-font: #{$plain};
    --md-filled-text-field-label-text-font: #{$plain};
    --md-sys-color-primary: rgb(var(--primary));
    --md-sys-color-surface-container-highest: rgb(var(--surfaceContainerLow));
    --md-filled-text-field-label-text-color: rgb(var(--onSurfaceVariant));
    --md-filled-text-field-input-text-color: rgb(var(--onSurface));
    margin-top: $group;
  }