@use '../styles/shape' as *;
@use '../styles/size' as *;
@use '../styles/elevation' as *;

@mixin layerstate($color, $opacity) {
    &::after {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: rgba(var(#{$color}), $opacity);
    }
  }

.button{
    position: relative;
    height: size(10);
    padding-right: size(8);
    padding-left: size(8);
    display: flex;
    align-items: center;
    justify-content: center;

&.filled {
    background-color: rgb(var(--primary));
    color: rgb(var(--onPrimary));
    border: none;
    border-radius: shape(corner-full);
    z-index: 1;
  
    &:hover {
        z-index: 2;
        @include layerstate(--onPrimary, 0.08);
        @include elevation(1);}

    &:focus {
        @include elevation(0);
        @include layerstate(--onPrimary, 0.12);
    }
    &:active {
        @include elevation(0);
        @include layerstate(--onPrimary, 0.12);
        }

  }

  &.outlined {
    color: rgb(var(--Primary));
    border: 1px solid rgb(var(--outline));
    border-radius: shape(corner-full);
    z-index: 1;
    overflow: hidden;
  
    &:hover {
        z-index: 2;
        @include layerstate(--primary, 0.08);             
    }
    &:focus {
        @include elevation(0);
        @include layerstate(--primary, 0.12);
    }
    &:active {
        @include elevation(0);
        @include layerstate(--primary, 0.12);
        }

  }

  
&.text {
    color: rgb(var(--primary));
    border: none;
    border-radius: shape(corner-full);
    z-index: 1;
    overflow: hidden;
  
    &:hover {
        z-index: 2;
        @include layerstate(--primary, 0.08);
    }
    &:focus {
        @include layerstate(--primary, 0.12);
    }
    &:active {
        @include layerstate(--primary, 0.12);
        }

  }
  
  &.elevated {
    background-color: rgb(var(--surfaceContainerLow));
    color: rgb(var(--primary));
    border: none;
    border-radius: shape(corner-full);
    z-index: 1;
    overflow: hidden;
    @include elevation(1);

    &.secondary{
        background-color: rgba(var(--primary), 0.5);
        color: rgb(var(--onPrimary));
    }
  
    &:hover {
        z-index: 2;
        @include layerstate(--primary, 0.08);
        @include elevation(2);}

    &:focus {
        @include elevation(1);
        @include layerstate(--primary, 0.12);
    }
    &:active {
        @include elevation(1);
        @include layerstate(--primary, 0.12);
        }

  }

  &.tonal {
    background-color: rgba(var(--secondaryContainer));
    color: rgb(var(--onSecondaryContainer));
    border: none;
    border-radius: shape(corner-full);
    z-index: 1;
    overflow: hidden;
  
    &:hover {
        z-index: 2;
        @include layerstate(--onSecondaryContainer, 0.08);
        @include elevation(1);}

    &:focus {
        @include elevation(0);
        @include layerstate(--onSecondaryContainer, 0.12);
    }
    &:active {
        @include elevation(0);
        @include layerstate(--onSecondaryContainer, 0.12);
        }
    }
  }