// breakpoints.scss (your existing breakpoints and mixin)
$breakpoints: (
  compact: (max: 599px),             // Width < 600px
  medium: (min: 600px, max: 839px),    // 600 ≤ width < 840px
  expanded: (min: 840px, max: 1199px), // 840 ≤ width < 1200px
  large: (min: 1200px, max: 1599px),   // 1200 ≤ width < 1600px
  extra-large: (min: 1600px)           // Width ≥ 1600px
);

@mixin window-size($breakpoint) {
  $bp: map-get($breakpoints, $breakpoint);

  @if map-has-key($bp, min) and map-has-key($bp, max) {
    @media (min-width: map-get($bp, min)) and (max-width: map-get($bp, max)) {
      @content;
    }
  } @else if map-has-key($bp, min) {
    @media (min-width: map-get($bp, min)) {
      @content;
    }
  } @else if map-has-key($bp, max) {
    @media (max-width: map-get($bp, max)) {
      @content;
    }
  }
}
