@use "sass:math";
@use "../styles/size" as *;

$star-size: 40px;
$star-count: 5;
$rating: 4.5; // Set the rating here (0.0 to 5.0)

$fill-percentage: math.div($rating, $star-count) * 100%;

.rating-display {
    /* Ensure the element is displayed, e.g., as a block-level element */
    display: inline-block;
  }
  
  .rating-display::after {
    content: " #{$rating}";
  }

.star-rating {
  width: calc(#{$star-size} * #{$star-count});
  height: $star-size;
  position: relative;
  display: inline-block;
  /* Base layer: empty (gray) stars.
     The SVG is encoded as a data URI.
     (Note: the '#' character in hex colors must be URL encoded as %23) */
  background: url("data:image/svg+xml;charset=UTF-8,<svg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 576 512'><path fill='%23e0e0e0' d='M259.3 17.8L194 150.2 47.9 171.5c-26.2 3.8-36.7 36.1-17.7 54.6l105.7 103-25 145.5c-4.5 26.3 23.2 46 46.4 33.7L288 439.6l130.7 68.7c23.2 12.2 50.9-7.4 46.4-33.7l-25-145.5 105.7-103c19-18.5 8.5-50.8-17.7-54.6L382 150.2 316.7 17.8c-11.7-23.6-45.6-23.9-57.4 0z'/></svg>")
    repeat-x;
  background-size: $star-size $star-size;

  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: $fill-percentage; // e.g. 90% for 4.5 stars out of 5
    height: 100%;
    /* Filled (yellow) stars overlay */
    background: url("data:image/svg+xml;charset=UTF-8,<svg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 576 512'><path fill='%23ffc107' d='M259.3 17.8L194 150.2 47.9 171.5c-26.2 3.8-36.7 36.1-17.7 54.6l105.7 103-25 145.5c-4.5 26.3 23.2 46 46.4 33.7L288 439.6l130.7 68.7c23.2 12.2 50.9-7.4 46.4-33.7l-25-145.5 105.7-103c19-18.5 8.5-50.8-17.7-54.6L382 150.2 316.7 17.8c-11.7-23.6-45.6-23.9-57.4 0z'/></svg>")
      repeat-x;
    background-size: $star-size $star-size;
    overflow: hidden;
  }
}

.review-star-individual{
    height: size(5);
    width: size(5);
    color: #ffc107;
}

.star-wrapper{
    display: flex;
    gap: size(1);
}