/* Layouts*/

@use '../styles/size' as *;
@use '../styles/theme' as *;
@use '../styles/typography';
@use '../styles/shape.scss' as *;
@use '../layouts/breakpoints' as *;

html, body {
  margin: 0;
  height: 100%;
}

a {
  text-decoration: none;
  color: inherit;
}

body{
  margin: 0;
  background: rgb(var(--surfaceContainerLowest));
  color: rgb(var(--onSurface));

}

.profile-image{
border-radius: shape(corner-full);
width: size(10);
height: size(10);
object-fit: cover;
object-position: center center; 
}

//Spacing, grouping and padding

.wrapper {
  display: flex;
  flex-direction: column;
  gap: var(--padding)
}

.gap-padding{
  gap: var(--padding) !important;
}

.gap-horizontal-padding{
  column-gap: var(--padding) !important;
}

.gap-margin{
  gap: var(--margin) !important;
}

.gap-spacer{
  gap: var(--spacer) !important;
}

.flex-space-between{
  display: flex;
  justify-content: space-between;
}



.padding{
  padding: var(--padding);
}

.group {
  gap: var(--group) !important;
}

.margin{
  margin: var(--margin);
}

//Footer

.footer-column{
  display: flex;
  flex-direction: column;
  gap: var(--group);
}

//Flex direction
.flex-column{
  display: flex;
  flex-direction: column;
}

.flex-row{
  display: flex;
}


//Alignment

.align-left{
  justify-content: left;
  align-items: flex-start;
}

.align-center{
  align-items: center;
}

.no-grow{
  flex: 0 0 auto;
}

.max-width-70ch{
  max-width: 70ch;
}

.max-width-50ch{
  max-width: 50ch;
}

.layout{
  display: grid;
  grid-template-columns: 0px 1fr;
}

.navigation{
  height: 100vh;
  display: flex;
  flex-direction: column;
  position: sticky;
  top: 0;
  border-right: 1px solid rgb(var(--outlineVariant));

}

.position-bottom{
  position: absolute;
  bottom: 10px;
}

.body{
  display: flex;
  align-items: stretch;   
  flex-direction: column;
  position: relative;
  max-width: 100%;
  overflow: hidden;
}

// Use hr as a break between sections
hr{
height: var(--spacer);
border-style: none;
margin: 0px;
padding: 0px;
}


// Layouts - Sections

section, footer{
  display: flex;
  flex-direction: column;
  align-items: center;
}
footer{
  border-top: solid 1px rgb(var(--outlineVariant));
  padding: var(--spacer) 0;
}

.breakout-right{
  margin-right: 0px;
  margin-left: 0px;
  overflow: auto;
  &::-webkit-scrollbar {
    display: none;  // Chrome, Safari, Opera
  }
  }

.container-1-pane{
  max-width: 1200px;
  width: 100%;
  box-sizing: border-box;
  position: relative;
  z-index: 2;
  gap: var(--padding);
  display: flex;
  flex-direction: column;
  padding: 0 var(--margin);

  
  &.filled{
    background-color: rgb(var(--surfaceContainerLow));
    padding: var(--margin);
    border-radius: shape(corner-large);
    gap: var(--padding);
  }

  &.gradient{
    background: linear-gradient(
      to bottom,
      rgb(var(--surfaceContainerLow)) 0%,
      rgba(var(--surfaceContainerLow), 0) 100%
    );
    padding: var(--margin) var(--margin) 0 var(--margin);
    border-radius: shape(corner-large);
    gap: var(--padding);
  }
}

.header {
  margin-top: 3.5rem;
  min-height: calc(85vh - 5rem);
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  position: absolute;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: rgb(255, 255, 255);

  &::after {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background: 
      linear-gradient(to top, rgba(var(--darkFixed), 1) 0%, transparent 40%),
      linear-gradient(to right, rgba(var(--darkFixed), 1) 1%, transparent 40%);
    pointer-events: none;
    z-index: 1;
  }

  @include window-size(compact) {
    min-height: 60vh;

    &::after {
      // This completely replaces the background on compact
      background: linear-gradient(to top, rgba(var(--darkFixed), 1) 0%, transparent 40%);
    }
  }
}


.header-container{
  z-index: 2;
  display: flex;
  align-items: flex-end;
  color: rgb(255, 255, 255);
  width: 100%;
  max-width: 1200px;
  height: calc(70vh - 2* var(--margin));
  margin-right: auto;
  margin-left: auto;

@include window-size(compact) {
  height: calc(60vh + size(12));
}


  }


.header-content-wrapper{
  z-index: 2;
  display: flex;
  flex-direction: column;
  gap: $group;
  & margin{
    margin: var(--margin);
  }
}

.content-wrapper-horizontal{
  display: flex;
  gap: $group;
  flex-wrap: wrap;
}

//Grids

.grid-4column{
  display: grid;
  gap: var(--padding);
  width: 100%;
  grid-template-columns: repeat(auto-fit, minmax(min(180px, 100%), 1fr));
}

.grid-footer {
  display: grid;
  gap: var(--margin);
  width: 100%;
  grid-template-columns: minmax(400px, 1fr) repeat(auto-fit, minmax(100px, 1fr));

  @media screen and (max-width: 800px) {
    grid-template-columns: repeat(auto-fit, minmax(min(200px, 100%), 1fr));

  }
}

.footer-first-column{
  max-width: 300px;
 @media screen and (min-width: 400px) and (max-width: 800px) {
    grid-column: 1 / -1; 
  }
}

.grid-3column{
  display: grid;
  gap: var(--padding);
  width: 100%;
  grid-template-columns: repeat(auto-fit, minmax(min(350px, 100%), 1fr));
}

.grid-2column{
  display: grid;
  width: 100%;
  grid-template-columns: repeat(auto-fit, minmax(min(400px, 100%), 1fr));
  gap: var(--padding);
}

.grid-item:first-child {
  grid-row: span 2;
}

.caurosel-container{
  display: flex;
  gap: $group;
  flex: 0 0 auto;
  border-radius: shape(corner-medium);
  -ms-overflow-style: none;  // IE and Edge
  scrollbar-width: none;     // Firefox
  &::-webkit-scrollbar {
    display: none;  // Chrome, Safari, Opera
  }

  @include window-size(compact) {
    padding-top: var(--padding);
  }

}

.caurosel{
  display: flex; 
  gap: $group; 
  flex: 0 0 auto;
  }

  .chip{
    background-color: rgb(var(--tertiaryContainer));
    color: rgb(var(--onTertiaryContainer));
    padding-left: size(4);
    padding-right: size(4);
    padding-top: size(2);
    padding-bottom: size(2);
    flex: 0 1 auto;  // 0 growth, 1 shrink, auto basis (size based on content)
  width: auto;     // Ensure width is determined by content
  /* Optionally, you can use align-self if you don't want it to stretch vertically: */
  align-self: flex-start;
  border-radius: shape(corner-full);
  white-space: nowrap;
  }

  .chip-outlined{
    padding-left: size(4);
    padding-right: size(4);
    padding-top: size(2);
    padding-bottom: size(2);
    flex: 0 1 auto;  // 0 growth, 1 shrink, auto basis (size based on content)
  width: auto;     // Ensure width is determined by content
  /* Optionally, you can use align-self if you don't want it to stretch vertically: */
  align-self: flex-start;
  border-radius: shape(corner-full);
  white-space: nowrap;
  border: solid 1px rgb(var(--outlineVariant));
  }

  .img-normal{
    width: 100%;
    height: auto;
    border-radius: shape(corner-medium);
  }


  .align-vertical-center{
    justify-content: center; /* centers items vertically */
  }

  .icon-wrapper-caption{
    display: flex;
    gap: $group;
    align-items: center;
    text-align: center;
  }

//Google Maps

  /* 
 * Always set the map height explicitly to define the size of the div element
 * that contains the map. 
 */
#map {
  height: 100%;
  border-radius: shape(corner-medium);
}

.heading-wrapper{
  display: flex;
  flex-direction: column;
  gap: $group;
  align-items: center;
  max-width: 70ch;
}

.margin-top-padding{
  margin-top: var(--padding);
}

#map{
  min-height: 50vh;
}