@use "../styles/theme" as *;
@use "../styles/size" as *;
@use "../styles/shape" as *;
@use "../styles/motion" as *;

details{
   border: solid 1px rgb(var(--outlineVariant));
   color: rgb(var(--onSurface));
   border-radius: shape(corner-extra-large);
overflow: hidden;}


summary{
    padding: $padding;
}

.accordion-content{
    padding: 0px $padding $padding $padding;
    color: rgb(var(--onSurfaceVariant));
}

summary::marker{
    color: rgb(var(--onSurfaceVariant));
}

details::details-content {
    block-size: 0;
    transition: 
        block-size motion(Standard, Short-4),
        content-visibility 1s;
    
    transition-behavior: allow-discrete;
}

details[open]::details-content {
    block-size: auto;
}